/**
 * Utilitaires pour la gestion des plannings
 */

/**
 * Format standard des donnÃ©es de planning
 *
 * Structure:
 * {
 *   employeeId: number,           // ID de l'employÃ©
 *   days: [                       // Tableau de 7 jours (lundi Ã  dimanche)
 *     {
 *       type: string,             // "work" ou "absence"
 *       hours: string,            // Nombre d'heures travaillÃ©es (format "0.0")
 *       absence: string,          // Type d'absence (congÃ©, maladie, etc.)
 *       note: string,             // Note ou commentaire
 *       timeSlots: [              // CrÃ©neaux horaires
 *         {
 *           start: string,        // Heure de dÃ©but (format "HH:MM")
 *           end: string,          // Heure de fin (format "HH:MM")
 *           break: string         // DurÃ©e de la pause en heures (optionnel)
 *         }
 *       ]
 *     }
 *   ]
 * }
 */

/**
 * Convertit les anciennes donnÃ©es de planning au format standard
 * @param {Object} schedule - DonnÃ©es de planning Ã  convertir
 * @returns {Object} - DonnÃ©es de planning au format standard
 */
export const standardizeScheduleData = (schedule) => {
  if (!schedule) return null;

  // Si le planning est dÃ©jÃ  au format standard, le retourner tel quel
  if (
    schedule.days &&
    Array.isArray(schedule.days) &&
    schedule.days.length > 0 &&
    schedule.days[0].type !== undefined
  ) {
    return schedule;
  }

  // Convertir au format standard
  return {
    employeeId: schedule.employeeId || schedule.employee_id,
    days: Array.isArray(schedule.days)
      ? schedule.days.map(convertDayToStandardFormat)
      : Array(7)
          .fill()
          .map(() => createEmptyDay()),
  };
};

/**
 * Convertit un jour de planning au format standard
 * @param {Object} day - Jour de planning Ã  convertir
 * @returns {Object} - Jour de planning au format standard
 */
export const convertDayToStandardFormat = (day) => {
  if (!day) return createEmptyDay();

  // Si le jour est dÃ©jÃ  au format standard, le retourner tel quel
  if (day.type) {
    return { ...day };
  }

  // Convertir au format standard
  return {
    type: day.absence ? "absence" : "work",
    hours: day.hours || "0",
    absence: day.absence || "",
    note: day.note || "",
    timeSlots:
      day.timeSlots ||
      (day.hours && parseFloat(day.hours) > 0
        ? [{ start: "09:00", end: "17:00" }]
        : []),
  };
};

/**
 * CrÃ©e un jour de planning vide au format standard
 * @returns {Object} - Jour de planning vide
 */
export const createEmptyDay = () => ({
  type: "work",
  hours: "0",
  absence: "",
  note: "",
  timeSlots: [],
});

/**
 * Calcule le total des heures pour un planning
 * @param {Object} schedule - DonnÃ©es de planning
 * @returns {number} - Total des heures
 */
export const calculateTotalHours = (schedule) => {
  if (!schedule || !schedule.days) return 0;

  return schedule.days.reduce((total, day) => {
    return total + (parseFloat(day.hours) || 0);
  }, 0);
};

/**
 * VÃ©rifie si un employÃ© est absent pour un jour donnÃ©
 * @param {Object} day - Jour de planning
 * @returns {boolean} - True si l'employÃ© est absent
 */
export const isAbsent = (day) => {
  return (
    day && day.type === "absence" && day.absence && day.absence.trim() !== ""
  );
};

/**
 * PrÃ©pare les donnÃ©es de planning pour l'envoi Ã  l'API
 * @param {Object} schedule - DonnÃ©es de planning au format standard
 * @returns {Object} - DonnÃ©es formatÃ©es pour l'API
 */
export const prepareScheduleForApi = (schedule) => {
  if (!schedule) return null;

  const standardSchedule = standardizeScheduleData(schedule);

  return {
    employee_id: standardSchedule.employeeId,
    schedule_data: JSON.stringify(standardSchedule.days),
    total_hours: calculateTotalHours(standardSchedule).toFixed(1),
  };
};

/**
 * Analyse les donnÃ©es de planning reÃ§ues de l'API
 * @param {Object} apiData - DonnÃ©es reÃ§ues de l'API
 * @returns {Object} - DonnÃ©es de planning au format standard
 */
export const parseScheduleFromApi = (apiData) => {
  if (!apiData) return null;

  let days;
  try {
    days =
      typeof apiData.schedule_data === "string"
        ? JSON.parse(apiData.schedule_data)
        : apiData.schedule_data;
  } catch (error) {
    console.error("Erreur lors de l'analyse des donnÃ©es de planning:", error);
    days = Array(7)
      .fill()
      .map(() => createEmptyDay());
  }

  return {
    employeeId: apiData.employee_id,
    days: Array.isArray(days)
      ? days.map(convertDayToStandardFormat)
      : Array(7)
          .fill()
          .map(() => createEmptyDay()),
  };
};

const scheduleUtils = {
  standardizeScheduleData,
  convertDayToStandardFormat,
  createEmptyDay,
  calculateTotalHours,
  isAbsent,
  prepareScheduleForApi,
  parseScheduleFromApi,
};

export default scheduleUtils;
