import { useCallback, useState } from "react";
import { toast } from "react-hot-toast";
import { HourBalanceService } from "../services/api";

/**
 * Hook personnalisÃ© pour gÃ©rer le solde d'heures des employÃ©s
 */
const useHourBalance = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  /**
   * RÃ©cupÃ¨re le solde d'heures d'un employÃ©
   * @param {number} employeeId - ID de l'employÃ©
   * @returns {Promise<Object>} - Solde d'heures de l'employÃ©
   */
  const getEmployeeBalance = useCallback(async (employeeId) => {
    setLoading(true);
    setError(null);

    try {
      const result = await HourBalanceService.getByEmployee(employeeId);

      if (!result.success) {
        console.warn(
          `Avertissement: Impossible de rÃ©cupÃ©rer le solde d'heures pour l'employÃ© ${employeeId}:`,
          result.message
        );
        return 0; // Retourner 0 par dÃ©faut au lieu de lancer une erreur
      }

      return result.balance;
    } catch (err) {
      console.warn(
        `Avertissement: Erreur lors de la rÃ©cupÃ©ration du solde d'heures pour l'employÃ© ${employeeId}:`,
        err
      );
      setError(
        err.message || "Erreur lors de la rÃ©cupÃ©ration du solde d'heures"
      );
      // Ne pas afficher de toast d'erreur pour ne pas perturber l'utilisateur
      return 0; // Retourner 0 par dÃ©faut
    } finally {
      setLoading(false);
    }
  }, []);

  /**
   * Met Ã  jour le solde d'heures d'un employÃ©
   * @param {number} employeeId - ID de l'employÃ©
   * @param {Object} balanceData - DonnÃ©es du solde d'heures
   * @returns {Promise<Object>} - RÃ©sultat de la mise Ã  jour
   */
  const updateEmployeeBalance = useCallback(async (employeeId, balanceData) => {
    setLoading(true);
    setError(null);

    try {
      const result = await HourBalanceService.updateBalance(
        employeeId,
        balanceData
      );

      if (!result.success) {
        throw new Error(
          result.message || "Erreur lors de la mise Ã  jour du solde d'heures"
        );
      }

      toast.success("Solde d'heures mis Ã  jour avec succÃ¨s");
      return { success: true, balance: result.balance };
    } catch (err) {
      console.error(
        `Erreur lors de la mise Ã  jour du solde d'heures pour l'employÃ© ${employeeId}:`,
        err
      );
      setError(
        err.message || "Erreur lors de la mise Ã  jour du solde d'heures"
      );
      toast.error("Erreur lors de la mise Ã  jour du solde d'heures");
      return { success: false, error: err.message };
    } finally {
      setLoading(false);
    }
  }, []);

  return {
    loading,
    error,
    getEmployeeBalance,
    updateEmployeeBalance,
  };
};

export default useHourBalance;
