import { useCallback, useEffect, useState } from "react";
import { toast } from "react-hot-toast";
import { WeeklyScheduleService } from "../services/api";
import {
  parseScheduleFromApi,
  prepareScheduleForApi,
  standardizeScheduleData,
} from "../utils/scheduleUtils";
import useWebSocket from "./useWebSocket";

/**
 * Hook personnalisÃ© pour gÃ©rer les plannings hebdomadaires
 */
const useWeeklySchedules = () => {
  const [schedules, setSchedules] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // IntÃ©gration WebSocket pour les mises Ã  jour en temps rÃ©el
  const { socket, isConnected, notifyDataChange } = useWebSocket();

  // Ã‰couter les mises Ã  jour WebSocket
  useEffect(() => {
    if (socket && isConnected) {
      // Fonction pour traiter les messages WebSocket
      const handleWebSocketMessage = (event) => {
        try {
          const data = JSON.parse(event.data);

          // Si le message concerne une mise Ã  jour de planning
          if (data.type === "SCHEDULE_UPDATED" && data.schedule) {
            setSchedules((prevSchedules) => {
              // VÃ©rifier si le planning existe dÃ©jÃ 
              const exists = prevSchedules.some(
                (schedule) => schedule.id === data.schedule.id
              );

              if (exists) {
                // Mettre Ã  jour le planning existant
                return prevSchedules.map((schedule) =>
                  schedule.id === data.schedule.id
                    ? parseScheduleFromApi(data.schedule)
                    : schedule
                );
              } else {
                // Ajouter le nouveau planning
                return [...prevSchedules, parseScheduleFromApi(data.schedule)];
              }
            });

            toast.info("Planning mis Ã  jour en temps rÃ©el");
          }

          // Si le message concerne une suppression de planning
          if (data.type === "SCHEDULE_DELETED" && data.scheduleId) {
            setSchedules((prevSchedules) =>
              prevSchedules.filter(
                (schedule) => schedule.id !== data.scheduleId
              )
            );

            toast.info("Planning supprimÃ© en temps rÃ©el");
          }
        } catch (error) {
          console.error(
            "Erreur lors du traitement du message WebSocket:",
            error
          );
        }
      };

      // Ajouter l'Ã©couteur d'Ã©vÃ©nements
      socket.addEventListener("message", handleWebSocketMessage);

      // Nettoyer l'Ã©couteur lors du dÃ©montage
      return () => {
        socket.removeEventListener("message", handleWebSocketMessage);
      };
    }
  }, [socket, isConnected]);

  /**
   * RÃ©cupÃ¨re tous les plannings pour une semaine donnÃ©e
   * @param {string} weekStart - Date de dÃ©but de semaine (format YYYY-MM-DD)
   * @returns {Promise<Array>} - Liste des plannings
   */
  const fetchSchedules = useCallback(async (weekStart) => {
    setLoading(true);
    setError(null);

    try {
      console.log(`RÃ©cupÃ©ration des plannings pour la semaine du ${weekStart}`);

      const result = await WeeklyScheduleService.getByWeek(weekStart);

      if (!result.success) {
        throw new Error(
          result.message || "Erreur lors du chargement des horaires"
        );
      }

      // Standardiser les donnÃ©es
      const standardizedSchedules = result.schedules.map((schedule) =>
        parseScheduleFromApi(schedule)
      );

      console.log("Plannings rÃ©cupÃ©rÃ©s:", standardizedSchedules);
      setSchedules(standardizedSchedules);
      return standardizedSchedules;
    } catch (err) {
      console.error("Erreur lors du chargement des horaires:", err);
      setError(
        "Erreur lors du chargement des horaires: " +
          (err.message || "Erreur inconnue")
      );
      toast.error("Erreur lors du chargement des horaires");
      return [];
    } finally {
      setLoading(false);
    }
  }, []);

  /**
   * CrÃ©e un nouveau planning
   * @param {Object} scheduleData - DonnÃ©es du planning
   * @returns {Promise<Object>} - RÃ©sultat de la crÃ©ation
   */
  const createSchedule = useCallback(
    async (scheduleData) => {
      try {
        // Standardiser et prÃ©parer les donnÃ©es pour l'API
        const apiData = prepareScheduleForApi(
          standardizeScheduleData(scheduleData)
        );

        console.log("DonnÃ©es envoyÃ©es Ã  l'API:", apiData);

        const result = await WeeklyScheduleService.create(apiData);

        if (!result.success) {
          throw new Error(
            result.message || "Erreur lors de la crÃ©ation de l'horaire"
          );
        }

        // Ajouter le nouveau planning Ã  la liste
        const newSchedule = parseScheduleFromApi(result.schedule);
        setSchedules((prev) => [...prev, newSchedule]);

        // Notifier les autres clients via WebSocket
        notifyDataChange("schedule", "create", newSchedule.id);

        toast.success("Planning crÃ©Ã© avec succÃ¨s");
        return { success: true, schedule: newSchedule };
      } catch (error) {
        console.error("Erreur lors de la crÃ©ation du planning:", error);
        toast.error(error.message || "Erreur lors de la crÃ©ation du planning");
        return { success: false, error: error.message };
      }
    },
    [notifyDataChange]
  );

  /**
   * Met Ã  jour un planning existant
   * @param {number} id - ID du planning
   * @param {Object} scheduleData - DonnÃ©es du planning
   * @returns {Promise<Object>} - RÃ©sultat de la mise Ã  jour
   */
  const updateSchedule = useCallback(
    async (id, scheduleData) => {
      try {
        // Standardiser et prÃ©parer les donnÃ©es pour l'API
        const apiData = prepareScheduleForApi(
          standardizeScheduleData(scheduleData)
        );

        console.log(`Mise Ã  jour du planning ${id}:`, apiData);

        const result = await WeeklyScheduleService.update(id, apiData);

        if (!result.success) {
          throw new Error(
            result.message || "Erreur lors de la mise Ã  jour de l'horaire"
          );
        }

        // Mettre Ã  jour le planning dans la liste
        const updatedSchedule = parseScheduleFromApi(result.schedule);
        setSchedules((prev) =>
          prev.map((schedule) =>
            schedule.id === id ? updatedSchedule : schedule
          )
        );

        // Notifier les autres clients via WebSocket
        notifyDataChange("schedule", "update", id);

        toast.success("Planning mis Ã  jour avec succÃ¨s");
        return { success: true, schedule: updatedSchedule };
      } catch (error) {
        console.error("Erreur lors de la mise Ã  jour du planning:", error);
        toast.error(
          error.message || "Erreur lors de la mise Ã  jour du planning"
        );
        return { success: false, error: error.message };
      }
    },
    [notifyDataChange]
  );

  /**
   * Supprime un planning
   * @param {number} id - ID du planning
   * @returns {Promise<Object>} - RÃ©sultat de la suppression
   */
  const deleteSchedule = useCallback(
    async (id) => {
      try {
        console.log(`Suppression du planning ${id}`);

        const result = await WeeklyScheduleService.delete(id);

        if (!result.success) {
          throw new Error(
            result.message || "Erreur lors de la suppression de l'horaire"
          );
        }

        // Supprimer le planning de la liste
        setSchedules((prev) => prev.filter((schedule) => schedule.id !== id));

        // Notifier les autres clients via WebSocket
        notifyDataChange("schedule", "delete", id);

        toast.success("Planning supprimÃ© avec succÃ¨s");
        return { success: true };
      } catch (error) {
        console.error("Erreur lors de la suppression du planning:", error);
        toast.error(
          error.message || "Erreur lors de la suppression du planning"
        );
        return { success: false, error: error.message };
      }
    },
    [notifyDataChange]
  );

  return {
    schedules,
    loading,
    error,
    fetchSchedules,
    createSchedule,
    updateSchedule,
    deleteSchedule,
  };
};

export default useWeeklySchedules;
